export default ()=> {
      return (
        <div className="question" data-index="148" dangerouslySetInnerHTML = {{ __html: `
<h6>148. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> user <span class="token operator">=</span> <span class="token punctuation">{</span>
	email<span class="token operator">:</span> <span class="token string">"e@mail.com"</span><span class="token punctuation">,</span>
	password<span class="token operator">:</span> <span class="token string">"12345"</span>
<span class="token punctuation">}</span>

<span class="token keyword keyword-const">const</span> <span class="token function-variable function">updateUser</span> <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token parameter"><span class="token punctuation">{</span> email<span class="token punctuation">,</span> password <span class="token punctuation">}</span></span><span class="token punctuation">)</span> <span class="token operator">=></span> <span class="token punctuation">{</span>
	<span class="token keyword keyword-if">if</span> <span class="token punctuation">(</span>email<span class="token punctuation">)</span> <span class="token punctuation">{</span>
		Object<span class="token punctuation">.</span><span class="token function">assign</span><span class="token punctuation">(</span>user<span class="token punctuation">,</span> <span class="token punctuation">{</span> email <span class="token punctuation">}</span><span class="token punctuation">)</span>
	<span class="token punctuation">}</span>

	<span class="token keyword keyword-if">if</span> <span class="token punctuation">(</span>password<span class="token punctuation">)</span> <span class="token punctuation">{</span>
		user<span class="token punctuation">.</span>password <span class="token operator">=</span> password
	<span class="token punctuation">}</span>

	<span class="token keyword keyword-return">return</span> user
<span class="token punctuation">}</span>

<span class="token keyword keyword-const">const</span> updatedUser <span class="token operator">=</span> <span class="token function">updateUser</span><span class="token punctuation">(</span><span class="token punctuation">{</span> email<span class="token operator">:</span> <span class="token string">"new@email.com"</span> <span class="token punctuation">}</span><span class="token punctuation">)</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>updatedUser <span class="token operator">===</span> user<span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>false</code></li>
<li>B: <code>true</code></li>
<li>C: <code>TypeError</code></li>
<li>D: <code>ReferenceError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p><code>updateUser</code> 函数更新user的 <code>email</code> 和 <code>password</code> 属性的值， 如果它们的值传入函数， 函数返回的就是 <code>user</code> 对象。 <code>updateUser</code> 函数的返回值是 <code>user</code> 对象，意味着updatedUser的值与 <code>user</code> 指向的是同一个 <code>user</code> 对象。<code>updatedUser === user</code> 为 <code>true</code>.</p>
</p>
</details>
` }}></div>
      );
    }